.fade-enter {
  opacity: 0;
}

.fade-enter-active {
  height: auto;
  opacity: 1;
  transition: opacity 300ms linear;
}

.fade-enter-done {
  height: auto;
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  height: 0;
  opacity: 0;
  transition: opacity 300ms linear;
}

.fade-exit-done {
  opacity: 0;
}

.fade-slide-enter {
  opacity: 0;
  transform: translateY(10px);
}

.fade-slide-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: all 300ms cubic-bezier(0.35, 0.35, 0.35, 0.98);
}

.fade-slide-exit {
  opacity: 1;
  transform: translateY(0);
  transition: all 300ms cubic-bezier(0.35, 0.35, 0.35, 0.98);
}

.fade-slide-exit-active {
  opacity: 0;
  transition: all 300ms linear;
}

.fade-slide-exit-done {
  opacity: 0;
}

.slide-in-enter,
.slide-in-appear {
  opacity: 0.2;
  transform: translateY(10px);
}

.slide-in-enter-active,
.slide-in-appear-active {
  opacity: 1;
  transform: translateY(0);
  transition: all 300ms cubic-bezier(0.35, 0.35, 0.35, 0.98);
}

.slide-in-exit,
.slide-in-exit-active {
  display: none;
}

.slide-enter,
.slide-appear {
  opacity: 0;
  transform: translateY(10px);
}

.slide-enter-active,
.slide-appear-active {
  opacity: 1;
  transform: translateY(0);
  transition: all 300ms cubic-bezier(0.35, 0.35, 0.35, 0.98) 300ms;
}

.slide-exit,
.slide-exit-active {
  opacity: 0;
  transform: translateY(10px);
  transition: all 300ms cubic-bezier(0.35, 0.35, 0.35, 0.98);
}

.slide-enter-done {
  display: block;
}

.slide-exit-done {
  display: none;
}

.scale-in-enter,
.scale-in-appear {
  opacity: 0;
  transform: scale(0.4);
  transform-origin: 100% 100%;
}

.scale-in-enter-active,
.scale-in-appear-active {
  opacity: 1;
  transform: scale(1);
  transform-origin: 100% 100%;
  transition: all 0.2s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.1s;
  transition-property: transform, opacity;
}

.scale-in-exit {
  opacity: 1;
  transform: scale(1);
}

.scale-in-exit-active {
  opacity: 0;
  transform: scale(0.4);
  transform-origin: 100% 100%;
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  transition-property: transform, opacity;
}

.slide-in {
  animation-delay: 0.15s;
  animation-duration: 0.2s;
  animation-fill-mode: both;
  animation-name: slideIn;
  animation-timing-function: cubic-bezier(0.35, 0.35, 0.35, 0.98);
  transform-origin: bottom;
  will-change: transform, opacity;
}

.fade-in,
.fade-in-left,
.fade-in-right {
  animation-delay: 0.15s;
  animation-duration: 0.3s;
  animation-fill-mode: both;
  animation-timing-function: ease-in;
}

.fade-in {
  animation-name: fadeIn;
}

.fade-in-right {
  animation-name: fadeInRight;
}

.fade-in-left {
  animation-name: fadeInLeft;
}

@keyframes animationPulse {
  0% {
    box-shadow: 0 0 0 0;
    opacity: 0.5;
  }

  40% {
    box-shadow: 0 0 0 8px;
    opacity: 0;
  }

  80% {
    box-shadow: 0 0 0 8px;
    opacity: 0;
  }

  100% {
    box-shadow: 0 0 0 0;
    opacity: 0;
  }
}

@keyframes animationSpin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes shake {

  10%,
  90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%,
  80% {
    transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%,
  60% {
    transform: translate3d(4px, 0, 0);
  }
}

@keyframes pop {
  from {
    opacity: 0;
    transform: scale(0.5);
    transform-origin: 50% 50%;
  }

  to {
    opacity: 1;
    transform: scale(1);
    transform-origin: 50% 50%;
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(10px);
  }

  to {
    opacity: 1;
    transform: none;
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-10px);
  }

  to {
    opacity: 1;
    transform: none;
  }
}

