.erxes-launcher-container {
  border-radius: 28px;
  bottom: 8px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
  cursor: pointer;
  height: 56px;
  left: auto;
  line-height: 56px;
  margin-left: auto;
  margin-top: auto;
  pointer-events: auto;
  position: fixed !important;
  right: 8px;
  text-align: center;
  top: auto;
  width: 56px;
  z-index: 2147483646;

  .erxes-launcher {
    position: absolute;
    top: 0px;
    left: 0px;
    background: rgb(0, 0, 0);
    width: 56px;
    height: 56px;
    border-radius: 50%;
    cursor: pointer;
    backface-visibility: hidden;
    overflow: hidden;
    -webkit-font-smoothing: antialiased;
    color-scheme: normal;
  }
}

.control-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
  list-style-type: none;
  height: 100%;
  width: 100%;
}

.hover-control-item {
  transform: scale(0);
  opacity: 0;
  position: absolute;
  transition:
    transform 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    opacity 0.8s;
  will-change: transform;

  svg {
    display: block;
  }

  &:nth-of-type(1) {
    bottom: 40px;
    left: 0;
  }

  &:nth-of-type(2) {
    right: 45px;
    top: -31px;
  }

  &:nth-of-type(3) {
    right: 65px;
    top: 14px;
  }
}

.launcher-hovered .hover-control-item {
  transform: scale(1);
  opacity: 1;
}

.hover-control-item>button {
  outline: none;
  border-radius: 100px;
  border: none;
  background: linear-gradient(170deg,
      rgba(141, 148, 255, 1) 0%,
      rgba(79, 51, 175, 1) 36%,
      rgba(102, 60, 248, 1) 63%,
      rgba(139, 115, 189, 1) 100%);
  cursor: pointer;
  padding: 0.56rem;

  &:hover {
    background-color: #ffffff10;
  }
}

