.nav-list {
  display: flex;
  list-style-type: none;
  align-items: center;
}

.nav-item {
  position: relative;
  height: 2.75rem;
  min-width: 2.75rem;
  cursor: pointer;
  transition: all ease 0.3s;

  &:hover {
    border-radius: 9999px;

    svg {
      fill: white;
    }

    .nav-content span {
      opacity: 1;
      visibility: visible;
      transform: translateX(0);
    }
  }

  &.active {
    background-color: transparent;

    .nav-content span {
      opacity: 1;
      visibility: visible;
      transform: translateX(0);
    }
  }

  .nav-content {
    height: 100%;
    width: 100%;
    justify-content: center;
    display: flex;
    align-items: center;
    gap: 0.5rem;

    span {
      font-size: 0.85rem;
      font-weight: 500;
      color: white;
      background-color: transparent;
      transition: all ease 0.3s;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;

      opacity: 0;
      visibility: hidden;
      transform: translateX(-4px);
      white-space: nowrap;
    }
  }
}

